import styled from 'styled-components'
import {MdKeyboardArrowRight,MdArrowForward} from 'react-icons/md';

export const HeroContainer=styled.div`

    background:#0c0c0c;
    display:flex;
    justify-content:center;
    align-items:center;
    padding:0;
    height:105vh;
    position:relative;
    z-index:1;
   
    :before{
        content:'';
        position:absolute;
        top:0;
        left:0;
        bottom:0;
        right:0;
        background:linear-gradient(180deg,rgba(0,0,0,0.4) 0%, rgba(0,0,0,0.6) 100%),
        linear-gradient(180deg,rgba(0,0,0,0.4) 0%, transparent 100%);

        z-index: 2;
    }

    @media screen and (max-width:760px){
        height:100vh;
    }

`;

export const HeroBg=styled.div`

    position:absolute;
    top:0;
    right:0;
    left:0;
    bottom:0;
    left:0;
    width:100%;
    height:100%;
    overflow:hidden;
`;

export const VideoBg=styled.img`
    width:100%;
    height:100%;
    -o-object-fit:cover;
    object-fit:cover;
    background:#232a34;
    filter:opacity(50%);
`;

export const HeroContent=styled.div`
    z-index:3;
    max-width:1200px;
    position:absolute;
    padding:8px 24px;
    display:flex;
    flex-direction:column;
    align-items:center;
    

`;


export const HeroH1=styled.strong`

        color:#fff;
        font-size:40px;
        font-weight:900;
        text-align:center;
        word-spacing:8px;
        margin-bottom:40px;
        filter:contrast(1.75);
        filter:saturate(4);
        text-shadow:0.3px 0.3px #f9f6ee;

        @media screen and (max-width: 760px) {

            font-size:18px;
            word-spacing:4px;

        }

        @media screen and (max-width:480px) {
         
                font-size:18px;
                 word-spacing:4px;
        }
`;

export const HeroP= styled.p`

    margin-top:24px;
    color:#fff;
    font-size:24px;
    text-align:center;
    max-width:600px;
        

     @media screen and (max-width: 760px) {

            font-size:24px;

        }

        @media screen and (max-width:480px) {
         
                font-size:18px;
        }
`;

export const HeroBtnWrapper=styled.div`
    margin-top:32px;
    display:flex;
    flex-direction:column;
    align-items:center;

`;

export const ArrowForward=styled(MdArrowForward)`
    margin-left:8px;
    font-size:20px;
`;

export const ArrowRight=styled(MdKeyboardArrowRight)`
    margin-left:8px;
    font-size:20px;

`;